html {
    box-sizing: border-box;
    font-size: 62.5%; 
} 

/* 1rem = 10px */

@media only screen and (max-width: 1200px) {
    html {
    font-size: 50%;
    }
}
 


@media only screen and (max-width: 768px) {
    html {
    font-size: 45%;
    }
}

@media only screen and (max-width: 425px) {
    html {
    font-size: 35%;
    }
}

/* 16px  */

body {
    font-family: 'Poppins', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
}


/* utility classes */

.hidden {
    display: none;
}

.absolute {
    position: absolute;
}

.relative {
    position: relative;
}

.header {
    display:  flex;
    flex-direction: column;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1)), url(../../partials/images/pawel-czerwinski-8uZPynIu-rQ-unsplash.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}


/* Navigation */

.navigation {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
    position: relative;
}

@media screen and (max-width: 768px) {
    .navigation {
        padding: 1rem;
    }
}



.logo {
    width: 150px;
    height: auto;
}

.nav {
    display: flex;
    flex-direction: row;
    
}

@media screen and (max-width: 768px) {
    .nav {
        display: none;
        
    }

    .nav_open {
        display: block;
    }
}


.nav_list {
    list-style-type: none;
    display: flex;
}

.nav_item {
    margin-right: 25px;
    font-size: 1.6rem;
}

.nav_link {
    text-decoration: none;
    color: black;
}

/* Mobile navigation */


.nav_mob {
    position: absolute;
    z-index: 1;
    top: 0%;
    left: 0px;
    right: 0px;
    padding: 1rem;
}

.nav_card {
    display: flex;
    flex-direction: column;
    border-radius: 0.8rem;
    background-color: white;
    overflow: hidden;
}

.nav_content {
    padding: 1.5rem 0 0.8rem ;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav_button {
    margin-right: 1.3rem;
}



.nav_open_icon {
    width: 3rem;
    height: auto;
    color: black;
    padding: 1rem;
    border-radius: 0.8rem;
}

.nav_close_icon {
    width: 3rem;
    height: auto;
    color: black;
    padding: 1rem;
    border-radius: 0.8rem;
}

.nav_logo {
    object-fit: cover;
    width: 15rem;
    height: 100%;
    padding-left: 1rem;
}



.nav_mob_item {
    margin-bottom:2rem;
    padding-top: 0.8rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
}

.nav_mob_item > a {
    display: block;
    padding: 1rem 1.5rem;
    text-decoration: none;
    color: black;
    border-radius: 0.8rem;
    padding-left: 1rem;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    font-size: 2.5rem;
}

/* Hero text */

.header_section {
    display: flex;
    flex-direction: column;
    max-width: 700px;
    margin: 0 auto;
    padding: 2rem;
}

.header_heading {
    font-weight: 400;
    font-size: 7rem;
    margin-bottom: 20px;
}

.header_para {
    font-weight: 400;
    font-size: 2.6rem;
    margin-bottom: 2rem;
}





/*  Buttons */


.btn {
    color: white;
    border: none;
    text-decoration: none;
    display: inline-block;
    font-size: 1rem;
    border-radius: 0.8rem;
}

.btn-sub {
    padding: 15px 30px;
    display: flex;
    align-items: flex-start;
    background-color: #D85461;
    font-weight: 700;
    font-size: 2rem;
}
